		kechengxinxiChat1() {
			this.$nextTick(()=>{

        var kechengxinxiChart1 = echarts.init(document.getElementById("kechengxinxiChart1"),'macarons');
        this.$http({
            url: "kechengxinxi/group/kechengmingcheng",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.pieNum){
						break;
					}
                    xAxis.push(res[i].kechengmingcheng);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].kechengmingcheng
                    })
                }
                var option = {};
				let titleObj = this.pie.title
				titleObj.text = ''
				
				const legendObj = this.pie.legend
				let tooltipObj = {trigger: 'item',formatter: '{b} : {c} ({d}%)'}
				tooltipObj = Object.assign(tooltipObj , this.pie.tooltip?this.pie.tooltip:{})
				
				let seriesObj = {
					type: 'pie',
					radius: '55%',
					center: ['50%', '60%'],
					data: pArray,
					emphasis: {
						itemStyle: {
							shadowBlur: 10,
							shadowOffsetX: 0,
							shadowColor: 'rgba(0, 0, 0, 0.5)'
						}
					}
				}
				seriesObj = Object.assign(seriesObj , this.pie.series)
                option = {
                	backgroundColor: this.pie.backgroundColor,
                	color: this.pie.color,
                	title: titleObj,
                	legend: legendObj,
                	tooltip: tooltipObj,
                	series: [seriesObj]
                };
                // 
                kechengxinxiChart1.setOption(option);
				
                  //
                window.onresize = function() {
                    kechengxinxiChart1.resize();
                };
            }
        });
      })
    },


